import React from 'react'

class TodoItem extends React.Component {

    constructor(){
        super()
        this.state = {

        }
    }

    render() {
        return (
            <div className="todo-item">
                <input
                    type="checkbox"
                    checked={props.item.completed}
                    onChange={() => props.handleChange(props.item.id)} 
                >
                </input>
                <p>{props.item.text}</p>
            </div>
        )
    }
}

export default TodoItem